import { useQuery } from '@tanstack/react-query'
import React from 'react'
import { getPoomStudentStatistic, getPoomUpStudentStatistic } from '../../../../api/Association/statisticAPI'
import LevelChart from '../../../../components/common/NivoCharts/LevelChart'
import { useDebug } from '../../../../hooks/basic/useDebug'

const PoomStudentStatistic = ({ year, month, region }) => {

  // query
  const { data: poomData } = useQuery(['getPoomStudentStatistic', year, month, region], () => getPoomStudentStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      let temp = []
      for (let i=0; i<3; i++) {
        temp.push({ level:`${i+1}품`, value: res[`poom_${i+1}_count`] })
      }
      for (let i=0; i<3; i++) {
        temp.push({ level:`${i+1}단`, value: res[`dan_${i+1}_count`] })
      }
      return temp.reverse()
    }
  })
  
  const { data: poomupData } = useQuery(['getPoomUpStudentStatistic', year, month, region], () => getPoomUpStudentStatistic(year, month, region), {
    enabled: !!year && !!month && !!region,
    select: (res) => {
      let temp = []
      for (let i=0; i<3; i++) {
        temp.push({ level:`${i+1}품`, value: res[`poom_${i+1}_levelup_count`] })
      }
      for (let i=0; i<3; i++) {
        temp.push({ level:`${i+1}단`, value: res[`dan_${i+1}_levelup_count`] })
      }
      return temp.reverse()
    }
  })

  // useDebug(poomData)

  return (
    <>
      <LevelChart data1={poomData} data2={poomupData} type="품" />
    </>
  )
}

export default PoomStudentStatistic